'use client';

import { Container, Title, SimpleGrid, Card, Image, Text, Badge, Button, Group } from '@mantine/core';

export default function Facilities() {
    const facilities = [
        {
            title: 'Auditorium (Main)',
            image: '/assets/auditorium_real_1.jpg', // Placeholder path, user needs to ensure assets exist or we use placeholders
            capacity: '500 Seats',
            features: ['4K Projector', 'Surround Sound', 'Stage Lighting'],
            status: 'Available',
        },
        {
            title: 'Medium Conference Room',
            image: '/assets/medium_conf_real_1.jpg',
            capacity: '50 Seats',
            features: ['Video Conferencing', 'Whiteboard', 'Smart TV'],
            status: 'In Use',
        },
        {
            title: 'Video Conference Room',
            image: '/assets/video_conf_real_1.jpg',
            capacity: '20 Seats',
            features: ['High-Speed Internet', 'Noise Cancellation', 'Dual Monitors'],
            status: 'Available',
        },
    ];

    return (
        <Container size="lg" py="xl">
            <Title order={1} mb="xl">Our Facilities</Title>
            <SimpleGrid cols={{ base: 1, sm: 2, md: 3 }} spacing="lg">
                {facilities.map((facility, index) => (
                    <Card key={index} shadow="sm" padding="lg" radius="md" withBorder>
                        <Card.Section>
                            <Image
                                src={facility.image}
                                height={160}
                                alt={facility.title}
                                fallbackSrc="https://placehold.co/600x400?text=No+Image" // Fallback if local assets missing
                            />
                        </Card.Section>

                        <Group justify="space-between" mt="md" mb="xs">
                            <Text fw={500}>{facility.title}</Text>
                            <Badge color={facility.status === 'Available' ? 'green' : 'red'}>
                                {facility.status}
                            </Badge>
                        </Group>

                        <Text size="sm" c="dimmed">
                            Capacity: {facility.capacity}
                        </Text>

                        <Text size="sm" c="dimmed" mt="xs">
                            Features: {facility.features.join(', ')}
                        </Text>

                        <Button color="blue" fullWidth mt="md" radius="md">
                            Book Now
                        </Button>
                    </Card>
                ))}
            </SimpleGrid>
        </Container>
    );
}
